<script setup>
import { ref, onMounted, computed, onBeforeUnmount } from 'vue';
import { useShowStore } from '@/stores/showStore';
import ShowCard from '@/components/shows/ShowCard.vue';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { faSearch, faTimes, faPlay, faInfoCircle } from '@fortawesome/free-solid-svg-icons';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

const showStore = useShowStore();
const isLoading = ref(true);
const error = ref(null);
const allShows = ref([]);

// Featured shows (hero section)
const featuredShows = ref([]);
const activeShowIndex = ref(0);

// Genres to display
const genres = [
  'Drama', 'Crime', 'Horror', 'Comedy', 'Action', 'Thriller',
  'Sci-Fi', 'Mystery', 'Romance', 'Adventure', 'Fantasy', 'Animation'
];

// Search functionality
const searchQuery = ref('');
const isSearchFocused = ref(false);

// Fetch all shows on component mount
onMounted(async () => {
  try {
    await showStore.initialize();
    
    // Get all shows and sort by rating
    allShows.value = Object.values(showStore.showsByGenre)
      .flat()
      .filter((show, index, self) => 
        index === self.findIndex((s) => s.id === show.id)
      )
      .sort((a, b) => (b.rating?.average || 0) - (a.rating?.average || 0));
      
    // Set featured shows (top 5 rated)
    featuredShows.value = allShows.value.slice(0, 5);
    
    // Initialize animations after data is loaded
    await initAnimations();
    
    // Auto-rotate featured shows
    if (featuredShows.value.length > 1) {
      setInterval(() => {
        activeShowIndex.value = (activeShowIndex.value + 1) % featuredShows.value.length;
      }, 8000);
    }
  } catch (err) {
    console.error('Error initializing shows:', err);
    error.value = 'Failed to load shows. Please try again later.';
  } finally {
    isLoading.value = false;
  }
});

// Initialize GSAP animations
const initAnimations = () => {
  // Animate show cards
  gsap.utils.toArray('.show-grid-item').forEach((card, i) => {
    gsap.from(card, {
      scrollTrigger: {
        trigger: card,
        start: 'top 90%',
        toggleActions: 'play none none none',
        once: true
      },
      opacity: 0,
      y: 30,
      duration: 0.6,
      ease: 'power2.out',
      delay: (i % 10) * 0.05
    });
  });

  // Animate genre sections
  gsap.utils.toArray('.genre-section').forEach((section, i) => {
    gsap.from(section, {
      scrollTrigger: {
        trigger: section,
        start: 'top 85%',
        toggleActions: 'play none none none',
        once: true
      },
      opacity: 0,
      y: 20,
      duration: 0.6,
      ease: 'power2.out',
      delay: i * 0.1
    });
  });
};

// Clean up GSAP animations
const cleanupAnimations = () => {
  ScrollTrigger.getAll().forEach(trigger => trigger.kill());
};

// Clean up on component unmount
onBeforeUnmount(() => {
  cleanupAnimations();
});

const handleSearch = () => {
  if (searchQuery.value.trim()) {
    // Handle search logic here
    console.log('Searching for:', searchQuery.value);
  }
};

const clearSearch = () => {
  searchQuery.value = '';
};

// Computed properties
const activeShow = computed(() => {
  return featuredShows.value[activeShowIndex.value] || null;
});

const getImageUrl = (image) => {
  return image?.original || 'https://via.placeholder.com/1600x500?text=No+Image+Available';
};

// Methods
const setActiveShow = (index) => {
  activeShowIndex.value = index;
};
</script>

<template>
  <div class="home-page" style="background: linear-gradient(to left, #0a0a0a 0%, #1a1a1a 100%); min-height: 100vh;">
    <!-- Loading State -->
    <div v-if="isLoading" class="flex justify-center items-center min-h-[50vh]">
      <div class="spinner-border text-primary" role="status">
        <span class="visually-hidden">Loading...</span>
      </div>
    </div>

    <!-- Error State -->
    <div v-else-if="error" class="error-state text-center py-5">
      <div class="text-danger fs-5 fw-medium mb-3">{{ error }}</div>
      <button 
        @click="$router.go()" 
        class="btn btn-primary px-4 py-2"
      >
        Retry
      </button>
    </div>

    <!-- Content -->
    <template v-else>
      <!-- Featured Show (Hero Section) -->
      <div v-if="featuredShows.length > 0" class="hero-section position-relative mb-5">
        <!-- Backdrop Image -->
        <div class="hero-backdrop position-absolute top-0 start-0 w-100 h-100">
          <img 
            v-if="activeShow.image?.original" 
            :src="activeShow.image.original" 
            :alt="activeShow.name"
            class="w-100 h-100 object-fit-cover"
          />
          <div class="backdrop-overlay position-absolute top-0 start-0 w-100 h-100"></div>
        </div>

        <!-- Show Info -->
        <div class="hero-content position-relative h-100 d-flex flex-column justify-content-end p-4 p-md-5 p-lg-5">
          <h1 class="display-4 fw-bold text-white mb-3 animate-on-scroll">
            {{ activeShow.name }}
          </h1>
          
          <div class="show-meta d-flex align-items-center mb-4">
            <span 
              v-if="activeShow.rating?.average"
              class="rating-badge badge bg-warning text-dark fw-bold me-3"
            >
              {{ activeShow.rating.average.toFixed(1) }}
            </span>
            <span class="text-light me-3" v-if="activeShow.premiered">
              {{ new Date(activeShow.premiered).getFullYear() }}
            </span>
            <div class="genres" v-if="activeShow.genres?.length">
              <span class="genre-badge" v-for="genre in activeShow.genres" :key="genre">
                {{ genre }}
              </span>
            </div>
          </div>
          
          <p class="show-description text-white-50 mb-4" v-if="activeShow.summary" v-html="activeShow.summary"></p>
          
          <div class="hero-actions d-flex gap-3 mb-4">
            <button class="btn btn-primary px-4 py-2 d-flex align-items-center">
              <font-awesome-icon :icon="['fas', 'play']" class="me-2" /> Watch Now
            </button>
            <button class="btn btn-outline-light px-4 py-2 d-flex align-items-center">
              <font-awesome-icon :icon="['fas', 'info-circle']" class="me-2" /> More Info
            </button>
          </div>
          
          <!-- Show Indicators -->
          <div class="show-indicators d-flex gap-2">
            <button 
              v-for="(show, index) in featuredShows" 
              :key="'indicator-' + show.id"
              @click="setActiveShow(index)"
              class="indicator-btn p-0 border-0 rounded-pill"
              :class="{ 'active': index === activeShowIndex }"
              :aria-label="'Go to slide ' + (index + 1)"
            ></button>
          </div>
        </div>
      </div>

      <!-- Main Content -->
      <div class="container mt-5">
        <!-- Search Bar -->
        <div class="search-bar mb-5 animate-on-scroll">
          <div class="position-relative">
            <input
              v-model="searchQuery"
              @keyup.enter="handleSearch"
              @focus="isSearchFocused = true"
              @blur="isSearchFocused = false"
              type="text"
              class="form-control form-control-lg bg-dark text-white border-secondary"
              placeholder="Search for TV shows..."
              :class="{ 'focused': isSearchFocused }"
            >
            <font-awesome-icon 
              v-if="!searchQuery" 
              :icon="['fas', 'search']" 
              class="position-absolute top-50 start-0 translate-middle-y ms-3 text-secondary"
            />
            <button
              v-else
              @click="clearSearch"
              class="btn btn-link position-absolute top-50 end-0 translate-middle-y me-3 p-0 text-secondary"
              type="button"
            >
              <font-awesome-icon :icon="['fas', 'times']" />
            </button>
          </div>
        </div>

        <!-- All Shows Section -->
        <section class="all-shows mb-5">
          <h2 class="section-title h3 fw-bold text-white mb-4">All Shows</h2>
          <div class="shows-grid">
            <div 
              v-for="(show, index) in allShows" 
              :key="'show-' + show.id"
              class="show-grid-item"
              :data-index="index"
            >
              <ShowCard :show="show" />
            </div>
          </div>
        </section>

        <!-- Genre Sections -->
        <template v-for="genre in genres" :key="'genre-' + genre">
          <section 
            v-if="allShows.some(s => s.genres?.includes(genre))" 
            class="genre-section mb-5"
          >
            <h2 class="section-title h3 fw-bold text-white mb-4">{{ genre }}</h2>
            <div class="shows-grid">
              <div 
                v-for="show in allShows.filter(s => s.genres?.includes(genre))" 
                :key="'genre-show-' + genre + '-' + show.id"
                class="show-grid-item"
              >
                <ShowCard :show="show" />
              </div>
            </div>
          </section>
        </template>
      </div>
    </template>
  </div>
</template>

