import { defineStore } from 'pinia';
import { ref, computed } from 'vue';
import axios from 'axios';

const API_BASE_URL = 'https://api.tvmaze.com';

export const useShowStore = defineStore('shows', () => {
  // State
  const showsByGenre = ref({});
  const searchResults = ref([]);
  const isLoading = ref(false);
  const error = ref(null);
  const featuredShows = ref([]);
  const cachedShows = ref({});
  
  // Getters
  const getShowsByGenre = (genre) => {
    return showsByGenre.value[genre] || [];
  };
  
  const getRandomFeaturedShows = (count = 5) => {
    // Flatten all shows from all genres
    const allShows = Object.values(showsByGenre.value).flat();
    
    // If we don't have enough shows, return what we have
    if (allShows.length <= count) {
      return allShows;
    }
    
    // Get random shows
    const shuffled = [...allShows].sort(() => 0.5 - Math.random());
    return shuffled.slice(0, count);
  };
  
  // Actions
  const fetchShowsByGenre = async (genre) => {
    // If we already have shows for this genre, don't fetch again
    if (showsByGenre.value[genre]?.length > 0) {
      return showsByGenre.value[genre];
    }
    
    isLoading.value = true;
    error.value = null;
    
    try {
      // First, try to get shows by genre (this might not be perfect with TVMaze API)
      const response = await axios.get(`${API_BASE_URL}/search/shows?q=${genre}`);
      
      // Filter and sort shows
      const shows = response.data
        .map(item => item.show)
        .filter(show => show.genres.includes(genre))
        .sort((a, b) => (b.rating?.average || 0) - (a.rating?.average || 0));
      
      // Cache the results
      showsByGenre.value = {
        ...showsByGenre.value,
        [genre]: shows
      };
      
      return shows;
    } catch (err) {
      console.error(`Error fetching ${genre} shows:`, err);
      error.value = `Failed to load ${genre} shows. Please try again later.`;
      return [];
    } finally {
      isLoading.value = false;
    }
  };
  
  const fetchAllShows = async () => {
    isLoading.value = true;
    error.value = null;
    
    try {
      // Get all shows (paginated)
      const response = await axios.get(`${API_BASE_URL}/shows`);
      
      // Process shows by genre
      const shows = response.data;
      const showsByGenreMap = {};
      
      shows.forEach(show => {
        if (!show.genres || !show.genres.length) {
          // If no genres, add to a default category
          showsByGenreMap['other'] = showsByGenreMap['other'] || [];
          showsByGenreMap['other'].push(show);
        } else {
          // Add show to each of its genres
          show.genres.forEach(genre => {
            const genreKey = genre.toLowerCase();
            showsByGenreMap[genreKey] = showsByGenreMap[genreKey] || [];
            showsByGenreMap[genreKey].push(show);
          });
        }
      });
      
      // Sort each genre's shows by rating
      Object.keys(showsByGenreMap).forEach(genre => {
        showsByGenreMap[genre].sort((a, b) => (b.rating?.average || 0) - (a.rating?.average || 0));
      });
      
      // Update state
      showsByGenre.value = showsByGenreMap;
      
      // Set featured shows (top rated from each genre)
      featuredShows.value = getRandomFeaturedShows(5);
      
      return showsByGenreMap;
    } catch (err) {
      console.error('Error fetching all shows:', err);
      error.value = 'Failed to load shows. Please try again later.';
      return {};
    } finally {
      isLoading.value = false;
    }
  };
  
  const searchShows = async (query) => {
    if (!query.trim()) {
      searchResults.value = [];
      return [];
    }
    
    isLoading.value = true;
    error.value = null;
    
    try {
      const response = await axios.get(`${API_BASE_URL}/search/shows?q=${encodeURIComponent(query)}`);
      searchResults.value = response.data.map(item => item.show);
      return searchResults.value;
    } catch (err) {
      console.error('Error searching shows:', err);
      error.value = 'Failed to search shows. Please try again later.';
      searchResults.value = [];
      return [];
    } finally {
      isLoading.value = false;
    }
  };
  
  const fetchShowById = async (id) => {
    // Check cache first
    if (cachedShows.value[id]) {
      return cachedShows.value[id];
    }
    
    isLoading.value = true;
    error.value = null;
    
    try {
      // Fetch show details
      const [showResponse, episodesResponse] = await Promise.all([
        axios.get(`${API_BASE_URL}/shows/${id}`),
        axios.get(`${API_BASE_URL}/shows/${id}/episodes`)
      ]);
      
      const showWithEpisodes = {
        ...showResponse.data,
        episodes: episodesResponse.data
      };
      
      // Cache the result
      cachedShows.value[id] = showWithEpisodes;
      
      return showWithEpisodes;
    } catch (err) {
      console.error(`Error fetching show with id ${id}:`, err);
      
      // If we can't get the full details, try to get at least the basic show info
      if (err.response?.status === 404) {
        throw new Error('Show not found');
      }
      
      // Try to get basic show info as fallback
      try {
        const basicShowResponse = await axios.get(`${API_BASE_URL}/shows/${id}`);
        return basicShowResponse.data;
      } catch (fallbackErr) {
        console.error(`Fallback fetch failed for show ${id}:`, fallbackErr);
        throw new Error('Failed to load show details');
      }
    } finally {
      isLoading.value = false;
    }
  };
  
  // Initial data fetch
  const initialize = async () => {
    await fetchAllShows();
  };
  
  return {
    // State
    showsByGenre,
    searchResults,
    isLoading,
    error,
    featuredShows,
    
    // Getters
    getShowsByGenre,
    getRandomFeaturedShows,
    
    // Actions
    fetchShowsByGenre,
    fetchAllShows,
    searchShows,
    fetchShowById,
    initialize
  };
});
